.waiting-list-wrapper,
.waiting-list-wrapper * {
  box-sizing: border-box;
}
.waiting-list-wrapper {
  display: flex;
  flex-direction: row;
  gap: 75px;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 95%;
  margin: 30px auto 20px auto;
}
.waiting-left-side {
  display: flex;
  flex-direction: column;
  gap: 31px;
  align-items: flex-start;
  justify-content: flex-start;
  flex-shrink: 0;
  position: relative;
}
.join-waiting-list {
  color: #000000;
  text-align: left;
  font-family: "Inter-Bold", sans-serif;
  font-size: 50px;
  font-weight: 700;
  position: relative;
  /* width: 462px; */
  width: 100%;
}
.be-the-first-to-know-join-our-exclusive-launch-notification-list-get-ready-for-the-launch-of-fidar-the-ultimate-collaboration-platform {
  color: #a6a6a6;
  text-align: left;
  font-family: "Inter-Regular", sans-serif;
  font-size: 18px;
  font-weight: 400;
  position: relative;
  /* width: 586px; */
  width: 100%;

}
.waiting-right-side {
  flex-shrink: 0;
  width: 410px;
  height: 60px;
  position: static;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.waiting-email::placeholder{
  color: #8b909a;
  text-align: left;
  font-family: "Inter-Medium", sans-serif;
  font-size: 18px;
  line-height: 30px;
  font-weight: 500;
  width: 184px;
}
.waiting-email-wrapper {
  width: 410px;
  height: 60px;
  position: static;
}
.waiting-email {
  border-radius: 70px;
  border-style: solid;
  border-color: #35b2f6;
  border-width: 2px;
  width: 410px;
  height: 60px;
  position: absolute;
  left: 661px;
  top: 38px;
}
.waiting-btn-wrapper {
  width: 162px;
  height: 60px;
  position: static;
  background-color: transparent;
  border: none;
  outline: none;
}
.join-now  {
  background: #35b2f6;
  border-radius: 70px;
  width: 162px;
  height: 60px;
  position: absolute;
  left: 909px;
  top: 38px;
  color: #ffffff;
  text-align: left;
  font-family: "Inter-Medium", sans-serif;
  font-size: 18px;
  line-height: 30px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media only screen and (max-width: 480px) {
  .waiting-list-wrapper {
    flex-direction: column;
    width: 100%;
  }
  .waiting-left-side {
    width: 100%;
  }
  .join-waiting-list {
    font-size: 38px;
    margin-bottom: -20px;
  }
  .be-the-first-to-know-join-our-exclusive-launch-notification-list-get-ready-for-the-launch-of-fidar-the-ultimate-collaboration-platform {
    font-size: 15px;
    width: 100%;
  }
  .waiting-right-side {
    width: 100%;
    margin-top: -40px;
  }
  .waiting-email-wrapper {
    position: relative;
  }
  .waiting-email {
    top: 0px;
    left: 0;
    width: 70%;
  }
  .waiting-email::placeholder {
    font-size: 14px;
  }
  .waiting-btn-wrapper {
    width: 50%;
    float: right;
    position: relative;
  }
  .join-now {
    left: 0;
    top: 0;
    font-size: 15px;
  }
}

