.profile-dropdown-menu {
    position: absolute;
    right: 0;
    top: 70px;
    background-color: white;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 0 8px 16px rgba(0,0,0,0.1);
    z-index: 99999;
  }

  .ellipse-1, .hello-femi-welcome {
    cursor: pointer;
  }
  
  .profile-dropdown-menu ul {
    list-style: none;
    padding: 10px;
    margin: 0;
  }
  
  .profile-dropdown-menu ul li {
    padding: 8px 16px;
    cursor: pointer;
  }

  .profile-dropdown-menu ul .profile-link {
    text-decoration: none;
    color: #2375a1;
    cursor: pointer;
  }
  
  .profile-dropdown-menu ul li:hover {
    background-color: #f1f1f1;
  }