.header-wrapper {
    display: flex;
    width: 90%;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    height: 40px;
    margin: 20px auto 0 auto;
}

.logo-wrapper {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  text-decoration: none;
  width: 6%;
  color: black;
}

.large-nav-wrapper {
  display: flex;
  flex-direction: row;
  gap: 130px;
  align-items: center;
  justify-content: flex-start;
  position: relative;
}
.link-container {
  display: flex;
  flex-direction: row;
  gap: 40px;
  align-items: center;
  justify-content: flex-start;
  flex-shrink: 0;
  position: relative;
}
.nav-link {
    color: #a6a6a6;
    text-align: left;
    font-family: "Inter-Medium", sans-serif;
    font-size: 16px;
    font-weight: 500;
    position: relative;
    text-decoration: none;
}
.nav-link-active {
  color: #000000;
  text-align: left;
  font-family: "Inter-Medium", sans-serif;
  font-size: 18px;
  font-weight: 500;
  position: relative;
}
.call-to-action-container{
  display: flex;
  flex-direction: row;
  gap: 20px;
  align-items: center;
  justify-content: flex-end;
  flex-shrink: 0;
  width: 258px;
  position: relative;
}
.cta-login {
  color: #a6a6a6;
  text-align: left;
  font-family: "Inter-Medium", sans-serif;
  font-size: 16px;
  font-weight: 500;
  position: relative;
  text-decoration: none;
}
.cta-signup {
  width: 80px;
  height: 40px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  text-decoration: none;
}
.cta-signup-text {
  background: #35b2f6;
  border-radius: 10px;
  width: 80px;
  height: 40px;
  color: #f8f8fa;
  text-align: center;
  font-family: "Inter-Medium", sans-serif;
  font-size: 16px;
  font-weight: 500;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

@media only screen and (max-width: 480px) {
.large-nav-wrapper {
    display: none;
}

.logo-wrapper {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  text-decoration: none;
  width: 22%;
  color: black;
}

    /* Base nav-wrapper styles */
.mobile-nav-wrapper {
    display: flex;
    justify-content: flex-end;
    position: relative;
  }
  
  /* Right-side mobile menu */
  .mobile-menu {
    position: fixed;
    top: 0;
    right: -100%; /* Offscreen initially */
    height: 100%;
    width: 220px;
    background-color: #fff;
    box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
    transition: right 0.3s ease-in-out;
    z-index: 100;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
  }
  
  .mobile-menu.open {
    right: 0; /* Slide in from the right */
  }
  
  /* Links styling */
  .link-container {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  
  /* Call to action buttons */
  .call-to-action-container {
    margin-top: auto;
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  /* Toggle button styles */
  .menu-toggle {
    position: absolute;
    top: -15px;
    right: 0px;
    cursor: pointer;
  }
  
  .burger {
    width: 30px;
    height: 4px;
    background-color: #333;
    margin: 5px 0;
    transition: transform 0.3s ease;
  }
  
  .burger.open:nth-child(1) {
    transform: rotate(45deg) translate(5px, 5px);
  }
  
  .burger.open:nth-child(2) {
    opacity: 0;
  }
  
  .burger.open:nth-child(3) {
    transform: rotate(-45deg) translate(5px, -5px);
  }

  .close-menu-btn {
    position: absolute;
    top: 10px;
    left: 10px;
    border: 1px solid #35b2f6;
    background-color: transparent;
    outline: none;
    font-size: 30px;
    font-weight: bold;
    color: #35b2f6;
  }
}



