/* General App container */
.app-container {
    padding: 20px;
    transition: background-color 0.3s ease, color 0.3s ease;
  }
  
  /* Light Mode */
  .light-mode {
    background-color: #fff;
    color: #000;
  }
  
  /* Dark Mode */
  .dark-mode {
    background-color: #000000;
    color: #ffffff;
  }
  
  /* Switch bar */
  .mode-switch {
    display: flex;
    align-items: center;
    cursor: pointer;
    margin-bottom: 20px;
  }
  
  .theme-bar {
    width: 50px;
    height: 25px;
    background-color: #ddd;
    border-radius: 15px;
    margin-right: 10px;
    position: relative;
  }
  
  .theme-slider-bar {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #fff;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    transition: transform 0.3s ease;
  }
  
  .slider-dark {
    transform: translate(25px, -50%);
    background-color: #000;
  }
  
  .slider-light {
    transform: translate(5px, -50%);
    background-color: #fff;
  }
  
  .theme-bar-label {
    font-size: 16px;
  }
  
  .theme-content {
    margin-top: 20px;
  }
  
  