.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .modal-content {
    background-color: #fff;
    padding: 20px;
    border-radius: 8px;
    width: 70%;
    max-height: 600px;
    overflow-x: hidden;
    overflow-y: auto;
    position: absolute;
    left: 300px;
  }
  
  .modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
  }
  
  .close-btn {
    cursor: pointer;
    background-color: transparent;
    border: none;
  }
  
  .conversation {
    margin-bottom: 20px;
    height: 250px;
    overflow: scroll;
    overflow-x: hidden;
    box-shadow: 0px 2px 7px 2px rgba(0, 0, 0, 0.1);
    padding: 0 4px;
  }
  
  .message {
    margin-bottom: 15px;
  }
  
  .sent {
    text-align: right;
  }
  
  .received {
    text-align: left;
  }
  
  .message-time {
    font-size: 0.8rem;
    color: gray;
  }
  
  .input-area {
    display: flex;
  }
  
  .input-area input {
    flex: 1;
    padding: 10px;
    margin-right: 5px;
    border-radius: 5px;
    border: 1px solid #ddd;
  }
  
  .input-area button {
    padding: 10px;
    border-radius: 5px;
    background-color: #007bff;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  @media only screen and (max-width: 480px) {
    .modal-content {
        width: 85%;
        left: 5px;
    }
  }

  @media only screen and (min-width: 481px) and (max-width: 768px) {
    .modal-content {
        width: 85%;
        left: 5px;
    }
  }

  @media only screen and (min-width: 769px) and (max-width: 1024px) {
    .modal-content {
        width: 85%;
        left: 5px;
    }
  }