.benefit-main-wrapper,
.benefit-main-wrapper * {
  box-sizing: border-box;
}
.benefit-main-wrapper {
  display: flex;
  flex-direction: row;
  gap: 0px;
  align-items: center;
  justify-content: space-around;
  position: relative;
  width: 90%;
  margin: 50px auto 60px auto;
}
.benefit-left-container {
  display: flex;
  flex-direction: column;
  gap: 50px;
  align-items: flex-start;
  justify-content: flex-start;
  flex-shrink: 0;
  position: relative;
}
.what-benefit-will-you-get {
  color: #191a15;
  text-align: left;
  font-family: "Inter-Bold", sans-serif;
  font-size: 50px;
  font-weight: 700;
  position: relative;
  width: 429px;
}
.benefit-list-items {
  display: flex;
  flex-direction: column;
  gap: 30px;
  align-items: flex-start;
  justify-content: flex-start;
  flex-shrink: 0;
  position: relative;
}
.benefit-list {
  display: flex;
  flex-direction: row;
  gap: 20px;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  position: relative;
}
.bullet-point {
  width: 32px;
  height: 32px;
}
.benefit-list-text {
  color: #000000;
  text-align: left;
  font-family: "Inter-Medium", sans-serif;
  font-size: 18px;
  font-weight: 500;
  position: relative;
}
.benefit-right-container {
  width: 649px;
  height: 543px;
  position: initial;
}
.benefit-right-top-icon {
  width: 40px;
  height: 40px;
  position: static;
}
.benefit-bg {
  background: #35b2f6;
  border-radius: 10px;
  width: 40px;
  height: 40px;
  position: absolute;
  left: 529px;
  top: 285.35px;
  transform-origin: 0 0;
  transform: rotate(-15deg) scale(1, 1);
}
.benefit-image-6 {
  width: 20px;
  height: 20px;
  position: absolute;
  left: 541.25px;
  top: 292.42px;
  overflow: visible;
}
.benefit-rectangle-48 {
  border-radius: 20px;
  width: 450px;
  height: 529px;
