@media only screen and (max-width: 480px) {
  .benefit-main-wrapper {
    flex-direction: column;
    width: 100%;
  }
  .benefit-left-container {
    width: 100%;
  }
  .what-benefit-will-you-get {
    width: 100%;
    font-size: 32px;
    margin-bottom: -15px;
  }
  .benefit-right-container {
    width: 100%;
    position: relative;
  }
  .benefit-rectangle-48 {
    position: initial;
    width: 100%;
  }
  .benefit-rectangle-58 {
    left: 0;
  }
  .benefit-ellipse-33 {
    left: 10px;
  }
  .amanda-young,.benefit-incoming-file  {
    left: 80px;
  }
  .benefit-ellipse-34 {
    left: 230px;
  }
  .benefit-message-circle-1 {
    left: 239px;
  }
  .benefit-rectangle-38 {
    top: 260px;
    left: 180px;
  }
  .data-analytics {
    top: 295px;
