
  .dropdown-menu {
    position: absolute;
    right: 10px;
    top: 45px;
    background: white;
    list-style: none;
    padding: 0;
    margin: 0;
    border: 1px solid #ccc;
    border-radius: 8px;
    width: 150px;
    z-index: 99999;
    background-color: white;
  }
  
  .dropdown-menu li {
    padding: 10px;
    cursor: pointer;
  }
  
  .dropdown-menu li:hover {
    background-color: #f0f0f0;
  }
  
  .selected-filter {
    position: absolute;
    right: 60px;
    top: 10px;
    font-weight: bold;
    color: #30a0dd;
  }

  @media only screen and (max-width: 480px) {
    .selected-filter {
        position: absolute;
        right: 35px;
        top: 13px;
        font-size: 13px;
        font-weight: bold;
        color: #30a0dd;
      }
  }

  @media only screen and (min-width: 481px) and (max-width: 768px) {
    .selected-filter {
        position: absolute;
        right: 45px;
        top: 13px;
        font-size: 13px;
        font-weight: bold;
        color: #30a0dd;
      }
  }

  @media only screen and (min-width: 769px) and (max-width: 1024px) {
    .selected-filter {
        position: absolute;
        right: 50px;
        top: 13px;
        font-size: 13px;
        font-weight: bold;
        color: #30a0dd;
      }
  }