.client-wrapper,
.client-wrapper * {
  box-sizing: border-box;
}
.client-wrapper {
  display: flex;
  flex-direction: column;
  gap: 16px;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  margin-top: 90px;
}
.client-section-title {
  color: #191a15;
  text-align: center;
  font-family: "Inter-Bold", sans-serif;
  font-size: 40px;
  font-weight: 700;
  position: relative;
}
.clients-logo-wrapper {
  display: flex;
  flex-direction: row;
  gap: 140px;
  align-items: center;
  justify-content: flex-start;
  flex-shrink: 0;
  position: relative;
}
.block {
  flex-shrink: 0;
  width: 145px;
  height: 36px;
  position: relative;
  overflow: hidden;
}
.threelines {
  flex-shrink: 0;
  width: 38px;
  height: 53px;
  position: relative;
  overflow: visible;
}
.animal-img {
  flex-shrink: 0;
  width: 43px;
  height: 48px;
  position: relative;
  overflow: visible;
}
.jigsaw-img {
  flex-shrink: 0;
  width: 71px;
  height: 55px;
  position: relative;
  overflow: hidden;
}

@media only screen and (max-width: 480px) {
    .client-wrapper {
        width: 100%;
        margin-top: 60px;
    }
    .client-section-title {
        font-size: 26px;
    }
    .clients-logo-wrapper {
        width: 100%;
        gap: 20px;
        justify-content: center;
    }
    .block {
        flex-shrink: 0;
        width: 40px;
        height: 40px;
        position: relative;
        overflow: hidden;
      }
      .threelines {
        flex-shrink: 0;
        width: 40px;
        height: 40px;
        position: relative;
        overflow: visible;
      }
      .animal-img {
        flex-shrink: 0;
        width: 40px;
        height: 40px;
        position: relative;
        overflow: visible;
      }
      .jigsaw-img {
        flex-shrink: 0;
        width: 40px;
        height: 40px;
        position: relative;
        overflow: hidden;
      }
}

