.preview-box {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 252px;
    height: 343px;
    margin: auto;
    background: #ffffff;
    border-radius: 5px;
    position: relative;
    box-shadow: var(
        --shadow-level-6-box-shadow,
        0px 8px 16px 0px rgba(96, 97, 112, 0.16)
    );
    overflow: hidden;
}

.uploaded-preview {
    width: 230px;
    height: 320px;
  }

.convert-button-wrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px;
    background: #35b2f6;
    border-radius: 4.31px;
    padding: 4.31px 57.75px 4.31px 57.75px;
    width: 178px;
    position: relative;
    box-shadow: 0px 24px 128px 0px rgba(0, 0, 0, 0.14);
}

.convert-button {
    color: #ffffff;
    text-align: center;
    font-family: "Poppins-Regular", sans-serif;
    font-size: 20px;
    font-weight: 400;
    border: none;
    outline: none;
    background-color: transparent;
}

/* style for convert modal */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    text-align: center;
    width: 100%;
    height: 100%;
  }
  
  .modal-content {
    background: white;
    padding: 20px;
    border-radius: 8px;
    text-align: center;
    width: 300px;
    text-align: center;
    transform: translate(50%); 
  }
  
  .modal-content p {
    font-size: 16px;
    margin-bottom: 20px;
  }
  
  .modal-buttons {
    display: flex;
    justify-content: space-around;
  }
  
  .accept-button,
  .decline-button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 10px 20px;
    font-size: 14px;
    cursor: pointer;
    border-radius: 4px;
  }
  
  .decline-button {
    background-color: #fff;
    color: #007bff;
    border: 1px solid #007bff;
  }
  
  .accept-button:hover {
    background-color: #0056b3;
  }
  
  .decline-button:hover {
    background-color: #f1f1f1;
  }
  