.accordion-wrapper,
.faccordion-wrapper * {
  box-sizing: border-box;
}
.accordion-wrapper {
  display: flex;
  flex-direction: column;
  gap: 23px;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 80%;
  margin: 70px auto 30px auto;
}
.faq {
  color: #191a15;
  text-align: center;
  font-family: "Inter-Bold", sans-serif;
  font-size: 40px;
  font-weight: 700;
  position: relative;
}
.accordion-title {
  color: #a6a6a6;
  text-align: left;
  font-family: "Inter-Regular", sans-serif;
  font-size: 18px;
  font-weight: 400;
  position: relative;
  width: 80%;
}
.accordion-container {
  flex-shrink: 0;
  width: 83%;
  height: fit-content;
  position: static;
  box-shadow: var(
    --cards-short-default-box-shadow,
    0px 5px 16px 0px rgba(8, 15, 52, 0.06)
  );
  padding: 10px 30px;
}

.faq-item {
  border-bottom: 1px solid #D9DBE9;
  padding: 25px 0;
}

.faq-question {
  color: #35b2f6;
  text-align: left;
  font-family: "Inter-Medium", sans-serif;
  font-size: 22px;
  line-height: 28px;
  font-weight: 500;
  width: 66.63%;
  height: 3.47%;
  cursor: pointer;
}

.faq-answer {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease-out;
  text-align: left;
  font-family: "Inter-Regular", sans-serif;
  font-size: 18px;
  font-weight: 400;
  width: 100%;
  height: 14.38%;
  background-color: transparent;
  color: #A6A6A6;
  margin-top: 10px;
}

.faq-answer.active {
  max-height: 500px; /* Set a height large enough to accommodate content */
  transition: max-height 0.5s ease-in;
}

.see-more {
  text-align: center;
  margin-top: 20px;
  color: #35b2f6;
  font-family: "Inter-Medium", sans-serif;
  font-size: 22px;
  line-height: 28px;
  font-weight: 500;
  text-decoration: underline;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.see-more a:hover {
  text-decoration: underline;
}

@media only screen and (max-width: 480px) {
  .accordion-wrapper {
    width: 100%;
  }
  .faq {
    font-size: 32px;
  }
  .accordion-title {
    width: 100%;
  }
  .accordion-container {
    width: 100%;
  }
  .faq-item {
    padding: 20px 0;
  }
  .faq-question {
    font-size: 18px;
  }
  .faq-answer {
    font-size: 16px;
  }
  .see-more {
    font-size: 18px;
  }
}

@media only screen and (min-width: 481px) and (max-width: 768px)  {
  .accordion-wrapper {
    width: 100%;
  }
  .faq {
    font-size: 32px;
  }
  .accordion-title {
    width: 100%;
  }
  .accordion-container {
    width: 90%;
  }
  .faq-item {
    padding: 20px 0;
  }
  .faq-question {
    font-size: 18px;
  }
  .faq-answer {
    font-size: 16px;
  }
  .see-more {
    font-size: 18px;
  }
}

@media only screen and (min-width: 769px) and (max-width: 1024px)  {
  .accordion-wrapper {
    width: 100%;
  }
  .faq {
    font-size: 32px;
  }
  .accordion-title {
    width: 100%;
  }
  .accordion-container {
    width: 80%;
  }
  .faq-item {
    padding: 20px 0;
  }
  .faq-question {
    font-size: 18px;
  }
  .faq-answer {
    font-size: 16px;
  }
  .see-more {
    font-size: 18px;
  }
}