.hero-wrapper,
.hero-wrapper * {
  box-sizing: border-box;
}
.hero-wrapper {
  display: flex;
  width: 95%;
  flex-direction: row;
  gap: 2px;
  align-items: flex-start;
  justify-content: center;
  position: relative;
  margin-top: 70px;
  margin-left: auto;
  margin-right: auto;
}
.hero-text {
  display: flex;
  flex-direction: column;
  gap: 69px;
  align-items: flex-start;
  justify-content: flex-start;
  flex-shrink: 0;
  position: relative;
  width: 55%;
}
.hero-title {
  color: #000000;
  text-align: left;
  font-family: "Inter-Bold", sans-serif;
  font-size: 64px;
  font-weight: 700;
  position: relative;
  width: 100%;
}
.sub-title-container {
  display: flex;
  flex-direction: column;
  gap: 50px;
  align-items: flex-start;
  justify-content: flex-start;
  flex-shrink: 0;
  position: relative;
}
.hero-curve {
  flex-shrink: 0;
  width: 479px;
  height: 26px;
  position: relative;
  overflow: visible;
  margin-left: -50px;
}
.sub-title-wrapper {
  display: flex;
  flex-direction: column;
  gap: 62px;
  align-items: flex-start;
  justify-content: flex-start;
  flex-shrink: 0;
  position: relative;
}
.hero-sub-title {
  color: #000000;
  text-align: left;
  font-family: "Inter-Regular", sans-serif;
  font-size: 32px;
  font-weight: 400;
  position: relative;
  width: 427px;
}
.hero-bottom {
  display: flex;
  flex-direction: row;
  gap: 40px;
  align-items: center;
  justify-content: flex-start;
  flex-shrink: 0;
  position: relative;
}
.hero-bottom-1 {
  background: #35b2f6;
  border-radius: 40px;
  padding: 20px 30px 20px 30px;
  display: flex;
  flex-direction: row;
  gap: 10px;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  position: relative;
  text-decoration: none;
  cursor: pointer;
}
.hero-bottom-1-text {
  color: #ffffff;
  text-align: left;
  font-family: "Inter-Medium", sans-serif;
  font-size: 18px;
  font-weight: 500;
  position: relative;
}
.hero-bottom-2 {
  display: flex;
  flex-direction: row;
  gap: 14px;
  align-items: center;
  justify-content: flex-start;
  flex-shrink: 0;
  position: relative;
  text-decoration: none;
  cursor: pointer;
}
.hero-icon-container {
  flex-shrink: 0;
  width: 40px;
  height: 40px;
  position: relative;
}
.hero-play {
  width: 79.17%;
  height: 79.17%;
  position: absolute;
  right: 10.42%;
  left: 10.42%;
  bottom: 10.42%;
  top: 10.42%;
  overflow: visible;
}
.hero-view-demo {
  color: #191a15;
  text-align: left;
  font-family: "Inter-Medium", sans-serif;
  font-size: 18px;
  font-weight: 500;
  position: relative;
}
.hero-image-container {
  flex-shrink: 0;
  width: 40%;
  height: 507px;
  position: static;
}
.hero-image {
  width: 575px;
  height: 507px;
  position: absolute;
  left: 619px;
  top: 0px;
  object-fit: cover;
}

@media only screen and (max-width: 480px) {
    .hero-wrapper {
        flex-direction: column;
        width: 100%;
    }
    .hero-text {
        width: 100%;

    }
    .hero-title {
        font-size: 34px;
    }
    .hero-curve {
        width: 70%;
        height: 26px;
        margin-left: 0px;
    }
    .sub-title-wrapper {
        width: 100%;
    }
    .hero-sub-title {
        font-size: 24px;
        width: 100%;
    }
    .hero-bottom-1 {
        padding: 10px 20px;
    }
    .hero-image-container {
        display: none;
    }
    .hero-view-demo {
        margin-left: -10px;
    }
    
}

@media only screen and (min-width: 481px) and (max-width: 768px) {
    .hero-wrapper {
